<template>
  <div>
    <table class="table table-hover" style="table-layout: fixed">
      <thead>
        <tr>
          <th scope="col">목표 이름</th>
          <th scope="col">성취 날짜</th>
          <th scope="col">진행률</th>
          <th scope="col">공개 여부</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td class="content " colspan="4" style="padding-left: 1em; text-align: left;">
            <RouterLink to="/goalmaker" style="text-decoration:none; color: rgb(6,90,92);font-weight: 500;">
              <img src="../assets/icones/plus-circle-fill.svg" style="width:2em;" alt="join a new circle"
                class="aligncenter text-success filter-green">&nbsp;&nbsp;나의 새로운 목표 만들기
            </RouterLink>
          </td>
        </tr>
        <tr v-for="(list, i) in state.lists" :key="(list, i)">
          <td class="content">
            <RouterLink :to="{name: 'goalread', params:{id:list.goalid }}"
              style="text-decoration:none;color: rgb(6,90,92);font-weight:500;">
              {{ list.name }}
            </RouterLink>
          </td>
          <td>{{ list.dueDate }}</td>

          <!-- <td>{{ list.successQ }}{{list.unit}}</td> -->
          <!-- <td>{{ list.qdone }}{{list.unit}}</td> -->

          <td>{{ list.perCentDone }}%</td>
          <td>
            <div class="form-check form-switch checkbox-in-the-middle">
              <input v-if="!list.goalPublic" class="form-check-input text-end" type="checkbox" role="switch"
                id="flexSwitchCheckDefault" @change="sendInfo(list.goalid, list.goalPublic)">
              <input v-else class="form-check-input text-center" type="checkbox" role="switch"
                id="flexSwitchCheckChecked" checked @change="sendInfo(list.goalid, list.goalPublic)">
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
<script>
import { onMounted } from 'vue'
import { reactive, ref } from '@vue/reactivity'
import axios from 'axios';
export default {
  setup() {
    onMounted(() => { loginHandler() })
    const state = reactive({
      lists: '',
      email: sessionStorage.getItem('email'),
      token: sessionStorage.getItem('TOKEN'),
    });
    const values = reactive({
      goalid: '',
      goalPublic: '',
    })
    const sendInfo = (goalid, goalPublic) => {
      values.goalid = goalid
      values.goalPublic = goalPublic
      privacyOption();
    }
    const loginHandler = async () => {
      const url = "http://43.201.60.125/api/goalList"
      const headers = {
        "Content-Type": "application/json",
        "Authorization": state.token,
        "token": state.token,
        "test": "test"
      }
      const body = { email: sessionStorage.getItem("email") }
      await axios.post(url, body, { headers }).then(function (res) {
        if (res.data != null) {
          state.lists = res.data
        } else {
          alert('too bad. check something')
        }
      })
    }
    const privacyOption = async () => {
      const url = "http://43.201.60.125/api/changePrivacyOption"
      const headers = {
        "Content-Type": "application/json",
        "Authorization": state.token,
        "token": state.token
      }
      const body = { goalid: values.goalid, goalPublic: !values.goalPublic }
      console.log("body.goalid: " + body.goalid + body.goalPublic)
      const response = await axios.post(url, body, { headers })
      if (response.status == 200) {
      } else ('fail')
    }
    return { state, loginHandler, privacyOption, sendInfo, values }
  }
}
</script>
<style scoped>
.checkbox-in-the-middle {
  padding-left: 5em;
}

.content {
  width: 10px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  word-break: break-all;
}

.filter-green {
  filter: invert(26%) sepia(23%) saturate(2821%) hue-rotate(143deg) brightness(90%) contrast(95%);
}
</style>